
.musicdetail {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 20;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  background: cover;
  background-size: cover;
  background: #aaa;
}
.musicdetail .filterbg {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  transition: all 0.5s;
  right: 0;
  z-index: 16;
  -webkit-filter: url("../../common/blur.svg#blur");
          filter: url("../../common/blur.svg#blur");
  -webkit-filter: blur(30px);
          filter: blur(30px);
}
.musicdetail .content {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 17;
  background: rgba(0,0,0,0.15);
}
.musicdetail .content .content-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 10vh;
  padding-top: 1vh;
  box-sizing: border-box;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.musicdetail .content .content-header .back,
.musicdetail .content .content-header .share {
  width: 50px;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50px;
          flex: 0 0 50px;
  height: 8vh;
  color: #fff;
  font-size: 24px;
  line-height: 8vh;
  text-align: center;
}
.musicdetail .content .content-header .share {
  font-size: 32px;
}
.musicdetail .content .content-header .musicTopDetail {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  color: #fff;
  width: calc(100% - 100px);
}
.musicdetail .content .content-header .musicTopDetail .singer,
.musicdetail .content .content-header .musicTopDetail .name {
  margin: 0;
  color: #fff;
  font-size: 12px;
  padding: 4px 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.musicdetail .content .content-header .musicTopDetail .name {
  font-size: 14px;
  font-weight: 400;
}
.musicdetail .content .content-wrapper {
  position: absolute;
  top: 10vh;
  left: 0;
  bottom: 20vh;
  right: 0;
  overflow: hidden;
}
.musicdetail .content .content-wrapper .cd .swith-line {
  width: 80%;
  height: 1px;
  background-image: linear-gradient(to left, rgba(255,255,255,0), rgba(255,255,255,0.6), rgba(255,255,255,0));
  margin: 0 auto;
}
.musicdetail .content .content-wrapper .cd .swith-line .triger {
  position: absolute;
  top: -14px;
  left: 50%;
  -webkit-transform: translate3d(-16px, 0, 0);
          transform: translate3d(-16px, 0, 0);
  width: 90px;
  height: 140px;
  background-image: url("/static/images/swith.png");
  background-size: cover;
  -webkit-transform-origin: 14px 16px;
          transform-origin: 14px 16px;
  transition: all 0.3s;
  z-index: 5;
}
.musicdetail .content .content-wrapper .cd .swith-line .triger.pause {
  -webkit-transform: translate3d(-16px, 0, 0) rotateZ(-30deg);
          transform: translate3d(-16px, 0, 0) rotateZ(-30deg);
}
.musicdetail .content .content-wrapper .cd .cd-content {
  width: 44vh;
  height: 44vh;
  position: relative;
  margin: 0 auto;
  margin-top: 70px;
  -webkit-animation: goRotate 16s linear infinite 0.1s;
          animation: goRotate 16s linear infinite 0.1s;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.musicdetail .content .content-wrapper .cd .cd-content.play {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}
.musicdetail .content .content-wrapper .cd .cd-content .cd-bg {
  width: 44vh;
  height: 44vh;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  background: url("/static/images/cd-mine.png");
  background-size: 100%;
  z-index: 2;
}
.musicdetail .content .content-wrapper .cd .cd-content .img {
  position: absolute;
  display: block;
  width: 28vh;
  height: 28vh;
  top: 8vh;
  transition: all 0.3s;
  left: 50%;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  z-index: 1;
}
.musicdetail .content .content-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 18vh;
  color: #fff;
  background-image: linear-gradient(to top, rgba(0,0,0,0.8), rgba(0,0,0,0.4), rgba(0,0,0,0), rgba(0,0,0,0));
}
.musicdetail .content .content-footer .div-range {
  height: 6vh;
}
.musicdetail .content .content-footer .musicDetailCtrl {
  height: 12vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 0;
}
.musicdetail .content .content-footer .musicDetailCtrl i {
  display: inline-block;
  font-size: 28px;
  width: 20vw;
  height: 50px;
  line-height: 50px;
  text-align: center;
}
.musicdetail .content .content-footer .musicDetailCtrl i.playType,
.musicdetail .content .content-footer .musicDetailCtrl i.menu {
  font-size: 20px;
  color: #e1e1e1;
}
.musicdetail .content .content-footer .musicDetailCtrl i.playPause {
  font-size: 46px;
}
.musicdetail.sliderUpHideRight-enter-to,
.musicdetail.sliderUpHideRight-leave-to {
  transition: all 0.3s;
}
.musicdetail.sliderUpHideRight-enter {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
  opacity: 0;
}
.musicdetail.sliderUpHideRight-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  opacity: 0;
}
@-webkit-keyframes goRotate {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes goRotate {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}


