
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.3);
    transform: scaleY(0.3);
}
}
.downloadimage {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  overflow: auto;
  opacity: 1;
  -webkit-overflow-scrolling: touch;
}
.downloadimage.fade-enter-to,
.downloadimage.fade-leave-to {
  transition: all 0.3s;
}
.downloadimage.fade-enter,
.downloadimage.fade-leave-to {
  opacity: 0;
}
.downloadimage .close {
  position: fixed;
  top: 30px;
  font-size: 20px;
  z-index: 10;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: right;
  right: 20px;
  color: rgba(255,255,255,0.8);
}
.downloadimage .imagecontent {
  position: relative;
  width: 100%;
  height: auto;
  padding-top: 80px;
  padding-bottom: 100px;
}
.downloadimage .imagecontent .imageArea {
  display: block;
  height: auto;
  width: 100%;
  padding: 0 36px;
  margin: 0 auto;
  box-sizing: border-box;
}
.downloadimage .imagecontent .imageArea .sheetimg {
  width: 180px;
  height: 180px;
  display: block;
  margin: 0 auto;
}
.downloadimage .imagecontent .imageArea .title {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  margin-top: 10px;
  text-align: center;
  line-height: 1.5;
  margin-top: 20px;
}
.downloadimage .imagecontent .detail {
  padding: 0 36px;
}
.downloadimage .imagecontent .detail .disc,
.downloadimage .imagecontent .detail .tips {
  color: #fff;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}
.downloadimage .imagecontent .detail .tips {
  margin-top: 16px;
}
.downloadimage .imagecontent .detail .tips .title {
  margin-right: 5px;
  font-size: 14px;
  font-weight: 400;
}
.downloadimage .imagecontent .detail .tips .span-tips {
  padding: 2px 8px;
  border: 1px solid rgba(244,244,244,0.6);
  margin-right: 10px;
  font-weight: 400;
  border-radius: 9px;
}
.downloadimage .swith-line {
  width: 80%;
  height: 1px;
  background-image: linear-gradient(to left, rgba(255,255,255,0), rgba(255,255,255,0.4), rgba(255,255,255,0));
  margin: 0 auto;
  margin-bottom: 15px;
}
.downloadimage .bottom {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 100px;
  z-index: 9;
}
.downloadimage .bottom .linearbg {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to top, rgba(0,0,0,0.4), rgba(0,0,0,0));
}
.downloadimage .bottom .linearbg .download {
  display: inline-block;
  width: auto;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 400;
  border: 1px solid rgba(244,244,244,0.4);
  position: absolute;
  left: 50%;
  top: 20%;
  color: #fff;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  border-radius: 12px;
}
.downloadimage .bottom .linearbg .download:active {
  background: rgba(0,0,0,0.2);
}
.downloadimage .top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 80px;
  z-index: 9;
}
.downloadimage .top .linearbg {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.4), rgba(0,0,0,0));
}


