.songsheet.sliderUpHideRight-enter-to,
.songsheet.sliderUpHideRight-leave-to {
  transition: all 0.3s;
}
.songsheet.sliderUpHideRight-enter {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
  opacity: 0;
}
.songsheet.sliderUpHideRight-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  opacity: 0;
