
.range {
  width: 90%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: flex;
  box-sizing: border-box;
  padding: 0 10px;
}
.range .rangeicon {
  font-size: 14px;
  margin-right: 10px;
}
.range .span-left {
  margin-right: 10px;
}
.range .duration {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 2px;
  border-radius: 1px;
  background: rgba(244,244,244,0.3);
  margin: 0 auto;
  font-size: 0;
  cursor: pointer;
}
.range .duration .currentProgress {
  display: inline-block;
  width: 0%;
  height: 2px;
  background: #f00;
  float: left;
}
.range .duration .ball {
  display: inline-block;
  width: 14px;
  height: 14px;
  margin-top: -6px;
  background: #fff;
  border-radius: 50%;
  float: left;
  cursor: pointer;
}
.range .span-right {
  margin-left: 10px;
}


