
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.border-1px::after {
    -webkit-transform: scaleY(0.3);
    transform: scaleY(0.3);
}
}
.musiclist .content {
  position: absolute;
  height: 314px;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 22;
}
.musiclist .content.sideUp-enter-to,
.musiclist .content.sideUp-leave-to {
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.musiclist .content.sideUp-enter,
.musiclist .content.sideUp-leave-to {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.musiclist .content .title {
  width: 100%;
  position: relative;
  height: 50px;
  font-size: 0;
}
.musiclist .content .title .play-type,
.musiclist .content .title .collect,
.musiclist .content .title .delete {
  display: inline-block;
  width: auto;
  height: 100%;
  line-height: 50px;
  padding: 0 15px;
}
.musiclist .content .title .play-type:active,
.musiclist .content .title .collect:active,
.musiclist .content .title .delete:active {
  background: #d5d5d5;
}
.musiclist .content .title .play-type {
  height: 100%;
}
.musiclist .content .title .play-type i {
  color: #666;
  font-size: 18px;
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
}
.musiclist .content .title .play-type span {
  color: #666;
  vertical-align: middle;
  font-size: 14px;
  font-weight: 300;
}
.musiclist .content .title .right {
  float: right;
  height: 100%;
  line-height: 50px;
}
.musiclist .content .title .right .collect,
.musiclist .content .title .right .delete {
  font-size: 12px;
  color: #666;
}
.musiclist .content .title .right .collect i,
.musiclist .content .title .right .delete i {
  font-size: 18px;
  vertical-align: middle;
}
.musiclist .content .title .right .collect span,
.musiclist .content .title .right .delete span {
  vertical-align: middle;
  font-size: 14px;
  font-weight: 300;
}
.musiclist .content .title .border-1px {
  position: relative;
}
.musiclist .content .title .border-1px:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.musiclist .content .container {
  height: calc(100% - 50px);
  margin: 0;
  padding: 0;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
  padding: 6px 0;
}
.musiclist .content .container li {
  margin: 0;
  padding: 0;
  list-style: none;
  height: 42px;
  line-height: 42px;
  padding: 0 15px;
  font-weight: 400;
  font-size: 14px;
}
.musiclist .content .container li .playingicon {
  font-size: 14px;
  vertical-align: middle;
  color: #c62f2f;
}
.musiclist .content .container li:active {
  background: #d5d5d5;
}
.musiclist .content .container li .border-1px {
  position: relative;
}
.musiclist .content .container li .border-1px:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.musiclist .mask {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 21;
  opacity: 1;
  background: rgba(0,0,0,0.5);
}
.musiclist .mask.fade-enter-to,
.musiclist .mask.fade-leave-to {
  transition: opacity 0.3s;
}
.musiclist .mask.fade-enter,
.musiclist .mask.fade-leave-to {
  opacity: 0;
}


