
.sidelist {
  position: relative;
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 0;
  padding: 0 15px;
  box-sizing: border-box;
}
.sidelist:active {
  background: #d5d5d5;
}
.sidelist .icon,
.sidelist .title {
  display: inline-block;
  vertical-align: middle;
}
.sidelist .icon {
  font-size: 16px;
  margin-right: 6px;
  color: #9a9a9a;
}
.sidelist .title {
  font-size: 14px;
  color: #333;
}
.sidelist .disc {
  position: absolute;
  right: 15px;
  font-weight: 400;
  top: 50%;
  -webkit-transform: translate3d(0, -50%, 0);
          transform: translate3d(0, -50%, 0);
  color: #9c9c9c;
  font-size: 10px;
}


